import React from 'react';
import { graphql, withPrefix, Link } from 'gatsby';
import Helmet from 'react-helmet';
import SEO from '../components/SEO';
import Layout from '../layouts/index';
import Call from '../components/Call';

const Home = props => {
  const markdown = props.data.allMarkdownRemark.edges;
  const json = props.data.allFeaturesJson.edges;
  return (
    <Layout bodyClass='page-home'>
      <SEO title='Home' />
      <Helmet>
        <meta
          name='description'
          content='With our pool service you dont need to do anything but enjoy your
          pool, we handle everything else.'
        />
      </Helmet>
      <div className='parallax'>
        <div className='intro pb-4'>
          <div className='container1' id='frost'>
            <h1 id='h1'>Pool cleaning and maintanence</h1>
            <p>
              With our pool service you don't need to do anything but enjoy your
              pool, we handle everything else.
            </p>
          </div>
        </div>
      </div>

      <div className='container pt-2'>
        <Call button />
      </div>

      <div className='container pt-8 pt-md-10'>
        <div>
          <div>
            <h1 className='title-3 text-dark mb-3'>Our Services</h1>
          </div>
          {markdown.map(edge => (
            <div
              key={edge.node.frontmatter.path}
              className='col-12 col-md-4 '
              id='flex'
            >
              <div className='card service service-teaser'>
                <div className='card-content'>
                  <h2>
                    <Link to={edge.node.frontmatter.path}>
                      {edge.node.frontmatter.title}
                    </Link>
                  </h2>
                  <p>{edge.node.excerpt}</p>
                </div>
              </div>
            </div>
          ))}
          <div className='col-12 text-center'>
            <Link className='button button-primary mt-2' to='/services'>
              View All Services
            </Link>
          </div>
        </div>
      </div>

      <div className='container pt-5 pb-5 pt-md-7 pb-md-7'>
        <div className='row justify-content-center'>
          <div className='col-12'>
            <h2 className='title-3 text-dark mb-4'>Our Features</h2>
          </div>
          {json.map(edge => (
            <div key={edge.node.id} className='col-12 col-md-6 col-lg-4 mb-2'>
              <div className='feature'>
                {edge.node.image && (
                  <div className='feature-image'>
                    <img src={withPrefix(edge.node.image)} />
                  </div>
                )}
                <h2 className='feature-title'>{edge.node.title}</h2>
                <div className='feature-content'>{edge.node.description}</div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </Layout>
  );
};

export const query = graphql`
  query {
    allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "/services/" } }
      sort: { fields: [frontmatter___date], order: DESC }
    ) {
      edges {
        node {
          id
          frontmatter {
            path
            title
            date(formatString: "DD MMMM YYYY")
          }
          excerpt
        }
      }
    }
    allFeaturesJson {
      edges {
        node {
          id
          title
          description
          image
        }
      }
    }
  }
`;

export default Home;
